<template>
    <div>
        <zoomMeetingSdk class="zoomVideoComponent" :meetingInfo="meetingInfo"
                        :forceOutActive="forceOutActive"
                        :hostEndActive="hostEndActive"
                        :userInfo="userInfo"
                        @zoomAction="zoomAction"
                        v-if="meetingInfoShow" role="0"></zoomMeetingSdk>
        <div id="zoom-user-live-pc">
            <userVideoComponent :meetingInfo="meetingInfo" :joinStartActive="joinStartActive" @outActive="outActive" v-if="meetingInfoShow"></userVideoComponent>
        </div>
        <iframe :src="testDebugUrl" id="testIframe" style="display:none" v-if="DEBUG && testDebugUrl"></iframe>
    </div>
</template>


<script>



    import AUTH from '@/config/web/auth'
    import userVideoComponent from "@/views/WEB/liveStream/userLive/component/userVideoComponent";
    import userLiveHandler from '../../handler/userLiveHandler';
    import userLiveComuHandler from '../../handler/userLiveComuHandler';
    import zoomMeetingSdk from "@/views/WEB/liveStream/zoomWebMeeting/zoomMeetingSdk";
    import hostLiveHandler from "@/views/WEB/liveStream/handler/hostLiveHandler";
    import JQ from "jquery";
    import apiService from "@/config/web/apiService";
    import {API_URL_BROADCAST_UPDATE_HOST_START_TIME} from "@/config/web/urls";


    export default {
        name: 'userVideo',
        components: {
            zoomMeetingSdk,
            userVideoComponent
        },
        data() {
            return {
                DEBUG: ((process.env.NODE_ENV == 'development') ? true : false),
                testDebugUrl: '',
                id: '',
                forceOutActive: false,
                hostEndActive: false,
                userInfo: {},
                meetingInfo: {},
                joinStartActive:false,
                meetingInfoShow: false,
            }
        },
        created() {

            this.id = this.$route.params.id;
            if (this.$isMobile()) {
                location.replace('/liveStream/userVideoMobile/' + this.id)
                return;
            }
            AUTH.setDevice(this.$isMobile());
            let member = AUTH.getSession();
            member = null;
            if (member && member?.memberInfo) {
                this.uid = member.memberInfo.uid;
                this.userName = member.memberInfo.name;
                this.userEmail = member.memberInfo.email;

            } else {

                this.uid = sessionStorage.getItem('joinUid');
                this.userName = sessionStorage.getItem('userName');
                this.userEmail = sessionStorage.getItem('userEmail');

            }

            this.userInfo = {
                uid: this.uid,
                userName: this.userName,
                userEmail: this.userEmail
            }
            if (this.uid && this.userName) {
                this.getMeetingInfo();

            } else {

            }

        },
        methods: {
            /**** 방송 시작 부분 ****/
            getMeetingInfo() {
                const params = {
                    id: this.id.toString(),
                    uid: this.uid,
                    userName: this.userName,
                    userEmail: this.userEmail
                }
                userLiveHandler.init(params, this, this.DEBGU);
                userLiveComuHandler.init(params, this);
                userLiveHandler.getLiveInfo((ret) => {

                    this.meetingInfo = ret;
                    this.meetingInfoShow = true;



                })
            },
            zoomAction(params) {
                switch (params.type) {
                    case 'start':
                        if (this.DEBUG) {
                            this.testDebugUrl = 'http://localhost/testUserJsonRpc/' + this.id + '/' + this.uid + '/' + this.meetingInfo.info.uid;
                        } else {
                            userLiveHandler.joinSocketRoom();
                        }
                        this.joinStartActive = true;
                        break;
                }
            },
            outActive() {
                this.forceOutActive = true;
            }

        }

    }
</script>
<style lang="scss">
    #zmmtg-root {
        width: calc(100vw);
        height: calc(100vh - 60px);
        margin-top: 60px;
        display: none;
    }
</style>

<style lang="scss">

    ul {
        padding: 0;
    }

    #zoom-user-live-pc {
        width: calc(100vw);
        height:  calc(100vh);
        position: relative;
        overflow-y: auto;
        overflow-x: hidden;

        .clear {
            clear: both;
        }

        #app-response {
            display: none;
        }

        #screen-init {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 60px;
            z-index: 100001;
            background: rgba(255, 255, 255, 0.1);
        }

        .header {
            width: 100%;
            padding: 0;
            maing: 0;
            border-bottom: solid 1px #C2C2C2;
            height: 60px;
            z-index: 100000;
            background: #fff;

            .logo {
                cursor: pointer;

                img {
                    max-width: 120px;
                    max-height: 50px;
                }
            }

            &.fixed {
                position: fixed;
                top: 0;
                left: 0;

                .logo {
                    cursor: default;
                }

            }

            .subject-box {

                width: auto;
                max-width: 370px;
                overflow-x: hidden;
                overflow-y: hidden;
                position: absolute;
                top: 15px;
                left: 150px;
                display: inline-block;
                height: 30px;

                text-align: center;
                line-height: 30px;
                font-size: 10pt;
                color: #000;
                font-weight: bold;

                .live-icon {
                    border-radius: 3px;
                    background: #E62118;
                    color: #fff;
                    padding: 3px 5px;

                }
            }

            .logo {
                position: absolute;
                top: 0;
                left: 0;
                width: 30%;
                text-align: left;
                font-size: 20pt;
                font-weight: bold;
                color: #1E90FF;
                float: left;
                padding: 0 0 0 20px;
                line-height: 60px;

                img {
                    margin-top: -5px;
                    max-width: 120px;
                    max-height: 50px;
                }
            }

            .menu {
                width: 600px;
                margin: auto;

                .menu-box {
                    margin-left: 100px;
                    display: flex;
                    list-style-type: none;

                    & > li {
                        text-align: center;
                        padding: 16px 15px 15px 15px;
                        font-weight: bold;
                        color: #000;
                        cursor: pointer;
                        position: relative;

                        img {
                            width: 30px;

                        }

                        .title-over {
                            display: none;
                            position: absolute;
                            z-index: 1000;
                            top: 49px;
                            left: 20px;
                            padding: 3px 10px;
                            min-width: 100px;
                            text-align: center;
                            background: #fafafa;
                            border: solid 1px #dcdcdc;
                            color: #000;
                            font-size: 9pt;

                            &.posi-top {
                                top: 7px;
                                left: 40px;
                            }
                        }

                        .is-dot {
                            display: inline-block;
                            position: absolute;
                            width: 10px;
                            height: 10px;
                            background: #ff0000;
                            border-radius: 5px;
                            right: 15px;
                            top: 15px;
                        }

                        .effect {
                            display: inline-block;
                            position: absolute;
                            width: 30px;
                            height: 30px;
                            background: #ff0000;
                            border-radius: 25px;
                            left: 15px;
                            top: 12px;
                        }

                        .sub-box {
                            display: none;
                            position: absolute;
                            top: 50px;
                            left: -40px;
                            min-width: 160px;
                            height: 80px;
                            -webkit-box-shadow: 1px 1px 5px -1px rgba(71, 70, 71, 0.53);
                            -moz-box-shadow: 1px 1px 5px -1px rgba(71, 70, 71, 0.53);
                            box-shadow: 1px 1px 5px -1px rgba(71, 70, 71, 0.53);
                            background: #fff;
                            border-radius: 5px;

                            &.webina {
                                max-height: 200px;
                                height: auto;
                                left: -25px;

                            }

                            &:hover {
                                display: block;
                            }

                            .sub-item {
                                font-size: 10pt;
                                font-weight: normal;
                                padding: 10px 0 10px 20px;
                                text-align: left;
                                color: #000;

                                &:hover {
                                    background: #F4F4F4;
                                }

                            }
                        }

                        &:hover {
                            color: #1E90FF;

                            .sub-box {
                                display: block;
                            }

                            .title-over {
                                display: block;
                            }
                        }
                    }

                }
            }

            .right-box {
                position: absolute;
                top: 0;
                right: 0;

                .right-ui {
                    display: flex;
                    list-style-type: none;

                    li {
                        padding: 12px 20px 0 0;

                        &.time-info {
                            padding: 20px 20px 0 0;
                            font-weight: bold;
                            font-size: 11pt;

                            span {
                                color: #1E90FF;
                            }
                        }

                        &.response-info {
                            padding: 8px 10px 0 0;

                            .layout-box {
                                display: none;
                                cursor: pointer;
                                border: solid 1px #ccc;
                                padding: 10px 5px;
                                border-radius: 8px;
                                font-weight: bold;
                                font-size: 11pt;

                                span {
                                    color: #1E90FF;
                                }

                                .circle-box {
                                    background: #999;
                                    color: #fff;
                                    width: 18px;
                                    height: 18px;
                                    display: inline-block;
                                    text-align: center;
                                    line-height: 16px;
                                    border-radius: 9px;

                                }
                            }
                        }

                        &.badRequest-box {
                            img {
                                width: 30px;
                                cursor: pointer;
                            }
                        }

                        &.userbox {
                            cursor: pointer;

                            div {
                                background: #F4F4F4;
                                color: #999999;
                                font-size: 14pt;
                                width: 38px;
                                height: 38px;
                                text-align: center;
                                line-height: 38px;
                                border-radius: 19px;
                                border: solid 1px #dcdcdc;
                            }
                        }

                        .end-button {
                            width: 130px;
                            height: 36px;
                            line-height: 36px;
                            border-radius: 18px;
                            background: #5E65E3;
                            color: #fff;
                        }
                    }

                }
            }
        }

        .screenLayout {
            position: fixed;
            top: 0;
            left: 0;
            width: calc(100vw);
            height: calc(100vh);
            z-index: 1000001;
            background: rgba(255,255,255,0.1);

            .name-box {
                width: 400px;
                height: 350px;
                -webkit-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                -moz-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                border-radius: 10px;
                position: absolute;
                left: 50%;
                top: 50%;
                background: #ffffff;
                margin: -160px 0 0 -200px;
                padding: 60px 20px 0 20px;
            }

            .screen-header {
                .del-box {
                    cursor: pointer;
                    position: absolute;
                    right: 15px;
                    top: 17px;
                    background: #999999;
                    width: 20px;
                    height: 20px;
                    text-align: center;
                    line-height: 20px;
                    border-radius: 10px;

                    i {
                        font-size: 10pt;
                        color: #fff;
                    }
                }
            }

            .summary {
                text-align: center;
                line-height: 150%;
                font-size: 11pt;
                color: #000;

            }

            .field-box {
                margin: 20px 0 0 0;

                .field {
                    width: 100%;
                }
            }

            .button-box {
                button {
                    width: 100%;
                    height: 50px;
                    background: #007aff;
                    color: #fff;
                    border-radius: 25px;
                }
            }

            &.login-box {
                .screen-title {
                    text-align: center;
                    font-weight: bold;
                    color: #000;
                    font-size: 12pt;
                    margin-bottom: 17px;
                }

                .button-row {
                    margin-top: 24px;
                    width: 100%;

                    button {
                        width: 48%;
                        height: 50px;
                        border-radius: 25px;

                        &:first-child {
                            margin-right: 4%;
                        }
                    }

                }

            }

        }

        .blackScreenLayout {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 100001;
            background: rgba(0, 0, 0, 0.3);
        }

        .bad-modal-box {
            width: 70%;
            max-width: 500px;
            height: 400px;
            margin: -130px auto 0 auto;
            background: #fff;
            position: relative;
            top: 50%;
            border-radius: 10px;

            h3 {
                padding: 30px;
            }

            .message {
                font-size: 13pt;
                color: #777;
                line-height: 130%;
                height: 200px;
                overflow-y: auto;
                padding: 0 30px;
            }

            .button-row {
                text-align: center;
                padding: 30px;

                button {
                    width: 100%;
                    border-radius: 25px;
                    height: 50px;
                }
            }
        }
    }

    .complete-content {
        width: 800px;
        height: 700px;
        -webkit-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
        -moz-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
        box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
        border-radius: 10px;
        position: absolute;
        left: 50%;
        top: 50%;
        background: #ffffff;
        margin: -350px 0 0 -400px;

        .title-box {
            height: 60px;
            line-height: 60px;
            border-bottom: solid 1px #dcdcdc;
            position: relative;

            .title {
                font-size: 12pt;
                font-weight: bold;
                color: #000;
                text-align: center;
            }

            .del-box {
                cursor: pointer;
                position: absolute;
                right: 15px;
                top: 17px;
                background: #999999;
                width: 20px;
                height: 20px;
                text-align: center;
                line-height: 20px;
                border-radius: 10px;

                i {
                    font-size: 10pt;
                    color: #fff;
                }
            }
        }

        .contents {
            background: #f4f4f4;
            padding: 0;
            height: 560px;
            overflow-y: auto;

            .img-box {
                width: 100%;
                height: 300px;
                background: #007aff;
                text-align: center;

                h3 {
                    margin: 0;
                    padding: 110px 0 10px 0;
                    font-size: 22pt;
                    color: #000000;
                    font-weight: bold;
                }

                .tip {
                    font-size: 10pt;
                    color: #fff;
                }
            }

            .text-box {
                .subject {
                    font-weight: bold;
                    color: #000;
                    font-size: 16pt;
                    padding: 15px 0 15px 30px;
                }

                .author {
                    color: #777;
                    font-size: 12pt;
                    padding: 0 0 0 30px;

                }

                .result-ul {
                    margin-top: 20px;
                    display: flex;
                    padding-left: 35px;

                    li {
                        width: 30%;
                        height: 130px;
                        border-radius: 5px;
                        -webkit-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                        -moz-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                        box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                        background: #fff;
                        margin-right: 5%;
                        padding: 20px;

                        .li-title {
                            color: #777;
                            font-size: 13pt;

                        }

                        .li-value {
                            padding-top: 15px;
                            font-weight: bold;
                            color: #2196F3;
                            font-size: 16pt;
                        }

                        &.last-child {
                            margin-right: 0;
                        }
                    }
                }
            }
        }

        .button-row {
            text-align: center;
            padding: 15px;

            button {
                min-width: 100px;
                border-radius: 25px;
                height: 50px;
                padding-left: 20px;
                padding-right: 20px;

                &:first-child {
                    margin-right: 30px;
                }
            }
        }
    }

    .board-layout {
        width: 800px;
        height: 700px;
        -webkit-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
        -moz-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
        box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
        border-radius: 10px;
        position: absolute;
        left: 50%;
        top: 50%;
        background: #ffffff;
        margin: -350px 0 0 -400px;

    }

    .exam-result-box {
        .content-box {
            text-align: center;

            .title-box {
                color: #000;
                font-weight: bold;
                font-size: 14pt;
            }

            .desc-box {
                color: #999;
                font-size: 11pt;
                margin: 30px 0;
                line-height: 140%;
            }
        }

        .button-row {
            button {
                width: 48%;
                height: 50px;
                border-radius: 25px;
                background: #5E65E3;
                color: #fff;

                &:first-child {
                    margin-right: 4%;
                }

                &.cancle {
                    background: #999;
                }
            }

            &.triple {
                button {
                    width: 32%;

                    &:first-child {
                        margin-right: 2%;
                    }

                    &:last-child {
                        margin-left: 2%;
                    }
                }
            }
        }

    }


</style>
